@import url("./index.scss");

.Feedback_body {
  @apply flex justify-center items-center gap-10 flex-col;
  .CompaniesFeedback {
    @apply flex justify-start items-center flex-col text-start;
    .CompaniesFeedback_title {
      @apply text-start flex justify-start items-start uppercase;
      font-size: 70px;
      width: 90%;
      font-weight: 900;
      opacity: 0;
      transform: translateX(-20%);
      transition: opacity 0.6s, transform 0.6s;
      animation: titleColorAnimation 5s infinite linear;
      text-shadow: 0 0 3px #7000ff;
    }
    .CompaniesFeedback_body {
      @apply flex justify-center items-center gap-5;
      width: 1600px;
      height: 400px;

      .CompaniesFeedback_card {
        width: 500px;
        height: 320px;
        border-radius: 25px;
        transition: 0.5s all;
        cursor: pointer;
        background: rgb(198, 175, 175);
        background: linear-gradient(162deg, rgba(198, 175, 175, 0.3) 24%, rgba(148, 52, 103, 0.3) 87%);
        padding: 25px;
        .CompaniesFeedback_company {
          @apply flex items-center gap-5;
          img {
            width: 120px;
          }
          .CompaniesFeedback_company_data {
            @apply flex flex-col;
            h1 {
              font-size: 13px;
              font-weight: 900;
            }
            h2 {
              font-size: 13px;
              color: var(--grey);
            }
          }
        }
        .CompaniesFeedback_underline {
          display: block;
          width: 100%;
          height: 1px;
          margin: 20px 0px;
          background-color: rgba(196, 196, 196, 0.5);
        }
        .CompaniesFeedback_data {
          font-size: 15px;
        }
        .CompaniesFeedback_user {
          @apply flex items-center gap-5;

          .CompaniesFeedback_user_btn {
            @apply flex justify-center items-center bg-white gap-1;
            font-size: 16px;
            border-radius: 15px;
            height: 40px;
            width: 40px;
            color: var(--black);
            position: relative;
            text-decoration: none;
            z-index: 1;
            transition: 0.5s all;

            img {
              transition: 0.5s all;
            }

            &:hover img {
              filter: brightness(0) invert(1);
            }

            &:before,
            &:after {
              content: "";
              position: absolute;
              z-index: -1;
            }
            &:after {
              height: 0;
              width: 100%;
              left: 0;
              background: var(--pink);
              transition: 0.5s;
            }
            &.--shine {
              overflow: hidden;
              &:after {
                height: 180%;
                width: 0;
                left: -95%;
                top: 0;
                background: var(--pink);
                transform: skew(50deg);
                transform-origin: top left;
                transition: 0.5s;
              }
              &:hover {
                color: var(--white);
                box-shadow: 0 0 5px var(--pink), 0 0 25px var(--pink);
                &:after {
                  width: 185%;
                }
              }
            }

            &:active {
              transform: translateY(15px);
            }
          }

          .CompaniesFeedback_user_data {
            @apply flex flex-col;
            h1 {
              font-size: 13px;
              font-weight: 900;
            }
            h2 {
              font-size: 13px;
              color: var(--grey);
            }
          }
        }
      }
      .c_1 {
        transform: translateX(-10%);
        opacity: 0;
        &:hover {
          transform: translateX(-5px) translateY(-5px) !important;
        }
      }
      .c_2 {
        transform: translateX(-20%);
        opacity: 0;
        &:hover {
          transform: scale(1.04);
        }
      }
      .c_3 {
        transform: translateX(-30%);
        opacity: 0;
        &:hover {
          transform: translateX(5px) translateY(-5px) !important;
        }
      }
    }
  }
  .YourFeedback {
    height: 600px;
    .YourFeedback_title {
      @apply text-center flex justify-center items-center uppercase;
      font-size: 70px;
      font-weight: 900;
      opacity: 0;
      transform: translateY(300px);
      transition: opacity 1.6s, transform 0.6s;
      animation: titleColorAnimation 5s infinite linear;
      text-shadow: 0 0 3px #7000ff;
    }
    .YourFeedback_body {
      @apply flex items-center justify-center gap-5;
      margin-top: 50px;
      opacity: 0;
      transition: 1.5s all;
      .YourFeedback_body_form {
        @apply flex justify-center text-start;
        background: rgb(198, 175, 175);
        background: radial-gradient(circle, rgba(198, 175, 175, 0.3) 0%, rgba(148, 52, 103, 0.2) 100%);
        border-radius: 25px;
        padding: 25px;
        width: 1100px;
        height: 500px;

        span {
          height: 460px;
          width: 1px;
          display: block;
          background-color: rgb(196, 196, 196, 0.5);
        }
        .YourFeedback_body_form_left {
          padding: 10px;
          width: 340px;
          .YourFeedback_body_title {
            font-size: 18px;
            width: 320px;
          }
          .YourFeedback_body_data {
            @apply flex flex-col items-center gap-5;
            margin: 25px 0px;
            input {
              color: var(--black);
              background-color: rgba($color: #ffffff, $alpha: 0.9);
              width: 320px;
              height: 40px;
              border-radius: 15px;
              padding: 10px;
              outline: none;
              border: 2px solid;
            }
            .YourFeedback_body_data_btn {
              @apply flex justify-center items-center bg-white gap-1;
              font-size: 16px;
              border-radius: 15px;
              height: 40px;
              width: 320px;
              color: var(--black);
              position: relative;
              text-decoration: none;
              z-index: 1;
              transition: 0.5s all;

              img {
                transition: 0.5s all;
              }

              &:hover img {
                filter: brightness(0) invert(1);
              }

              &:before,
              &:after {
                content: "";
                position: absolute;
                z-index: -1;
              }
              &:after {
                height: 0;
                width: 100%;
                left: 0;
                background: var(--pink);
                transition: 0.5s;
              }
              &.--shine {
                overflow: hidden;
                &:after {
                  height: 100%;
                  width: 0;
                  left: -27%;
                  top: 0;
                  background: var(--pink);
                  transform: skew(50deg);
                  transform-origin: top left;
                  transition: 0.5s;
                }
                &:hover {
                  box-shadow: 0 0 5px var(--pink), 0 0 25px var(--pink);
                  color: var(--white);
                  &:after {
                    width: 125%;
                  }
                }
              }

              &:active {
                transform: translateY(15px);
              }
            }
          }
        }
        .YourFeedback_body_form_right {
          @apply flex justify-center items-center gap-6;
          padding: 10px;
          width: 720px;
          .YourFeedback_body_form_image {
            img {
              width: 280px;
              height: auto;
              border-radius: 15px;
              box-shadow: 5px 5px 0px 1px rgba(0, 0, 0, 1);
              animation: 2s boxShadowGlow infinite linear;
            }
          }
          .YourFeedback_body_form_data {
            @apply flex flex-col items-center justify-between text-start;
            height: 420px;
            .YourFeedback_body_form_text {
              width: 350px;
              font-size: 18px;
              font-weight: 600;
            }
            .YourFeedback_body_form_details {
              width: 350px;
              @apply text-start;
              p {
                font-size: 14px;
                color: var(--grey);
              }
              .YourFeedback_body_form_details_experience {
                @apply flex items-center gap-5;
                margin-top: 20px;
                .Experience_years,
                .Experience_count {
                  @apply flex;
                  h1 {
                    position: relative;
                    text-transform: uppercase;
                    font-weight: 900;
                    background-clip: text;
                    color: transparent;
                    z-index: 1000;
                    font-size: 50px;
                    transition: 0.5s all;
                    background-image: -webkit-linear-gradient(left, #7000ff, #f13dd4, #7000ff);
                    background-size: 1200px 100px;
                    animation: colorChange 5s infinite;
                    z-index: 2;
                  }
                  p {
                    font-size: 12px;
                  }
                }
              }
            }
          }
        }
      }
      .YourFeedback_body_adress {
        background: rgb(198, 175, 175);
        background: radial-gradient(circle, rgba(198, 175, 175, 0.3) 0%, rgba(148, 52, 103, 0.2) 100%);
        border-radius: 25px;
        padding: 25px;
        width: 400px;
        height: 700px;
      }
    }
  }
}

.CompaniesFeedback_title.animate-slide-in {
  position: relative !important;
  opacity: 1;
  transform: translateX(0%) !important;
}
.c_1.animate {
  position: relative !important;
  transform: translateX(0) !important;
  opacity: 1 !important;
  transition: opacity 0.6s, transform 0.6s;
}
.c_2.animate {
  position: relative !important;
  transform: translateX(0) !important;
  opacity: 1 !important;
  transition: opacity 0.6s, transform 0.6s;
}
.c_3.animate {
  position: relative !important;
  transform: translateX(0) !important;
  opacity: 1 !important;
  transition: opacity 0.6s, transform 0.6s;
}
.YourFeedback_title.animate {
  position: relative !important;
  opacity: 1 !important;
  transform: translateY(0px) !important;
}

.YourFeedback_body.animate {
  position: relative !important;
  opacity: 1 !important;
}
